body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
  color: #333;
}
@media (max-width: 1200px) {
  body {
    font-size: 14px;
  }
}
p {
  color: #666;
  line-height: 28px;
  text-align: justify;
}
@media (max-width: 1200px) {
  p {
    line-height: 26px;
  }
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Playfair Display", serif;
  color: #282e3f;
}
ul {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
}
a {
  text-decoration: none;
  transition: all 0.2s;
}
a:hover {
  text-decoration: none;
}
img {
  max-width: 100%;
}

button,
a:focus {
  outline: none;
}

.btnStyle a,
.btnStyle button {
  background-color: #c0b596;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  padding: 10px 38px;
  border: 2px solid transparent;
  text-transform: capitalize;
  display: inline-block;
  margin-top: 3px;
}
