  .btnStyle a,
  .btnStyle button {
    padding: 10px 13px;
  }
}
.btnStyle a:hover,
.btnStyle button:hover {
  background: #d4c291;
}
.btnStyle3 a {
  position: relative;
}
.btnStyle3 a:before {
  content: "";
  position: absolute;
  right: -60px;
  top: 50%;
  width: 85px;
  height: 2px;
  background: #fff;
  bottom: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
}
@media (max-width: 1200px) {
  .btnStyle3 a {
    width: 60px;
    right: -40px;
  }
